import React,{Component} from 'react'
import 'whatwg-fetch'
import 'es6-promise'
class MyFetch extends Component{
    constructor(props){
        super(props)
        this.state={
            dbs:[]
        }
        this.getData=this.getData.bind(this)
    }
    getData(){
        var url='../../server/db';
        fetch(url,{
            method:'GET',
            mode:'cors',// cors
            credentials: 'include'
        }).then(function(response) {
            //json
            response.json().then(function(data){
                console.log(data);
            });
        }).catch(function(e) {
            console.log("Oops, error");
        });

    }
    render(){
        const dbs = this.state.dbs;
        return (
            <div>
                <button onClick={() =>{this.getData.bind(this)}}></button>

            </div>
        )
    }
}
export default MyFetch;


