import React, { Component } from 'react';
import './News_list.css';

export default class Ques_temp extends Component{
	constructor(props){
    super(props);
    this.state = {
      choose_num: this.props.choose
    }
  }
  onChangeNum(e){
    this.setState({
      choose_num: e
    })
  }
  onChangeNumToo(){
    this.setState({
      choose_num: this.props.choose
    })
  }
  onChangeBody(){
    this.props.onChange(this.state.choose_num)
  } 
	render(){
		return(
			<div id="news_list">
        <div className="col-md-12 col-sm-12">
          <h2 ></h2>
          <h4 ></h4>
          <button className="create_news" onMouseOver={() => {this.onChangeNum(3)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}></button> 
          <table className="col-md-12 col-sm-12">
            <thead>
              <tr>
                <th rowSpan="1" colSpan="1" style={{'width':140}}></th>
                <th rowSpan="1" colSpan="1" style={{'width':200}}></th>
                <th rowSpan="1" colSpan="1" style={{'width':160}}></th>
                <th rowSpan="1" colSpan="1" style={{'width':160}}></th>
                <th rowSpan="1" colSpan="1" style={{'width':100}}></th>
                <th rowSpan="1" colSpan="1" style={{'width':180}}></th>
              </tr>
            </thead>
            <tbody id="tbody">
              <tr>
                <td>2015</td>
                <td>2015</td>
                <td>2016/6/01 08:00 </td>
                <td>2017/6/03 08:00 </td>
                <td></td>
                <td>
                  <button className="action"></button>                                                                        
                  <button className="action"></button>
                </td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td>2017/1/04 08:00 </td>
                <td>2017/1/06 08:00 </td>
                <td></td>
                <td>
                  <button className="action"></button>                                                                        
                  <button className="action"></button>
                </td>
              </tr>
              <tr>
                <td>2018</td>
                <td></td>
                <td>2017/7/01 08:00 </td>
                <td>2020/2/11 08:00 </td>
                <td></td>
                <td>
                  <button className="action"></button>                                                                        
                  <button className="action"></button>
                </td>
              </tr>
            </tbody>
          </table>
          <br />
          <div className="row">
            <div className="col-md-5 col-sm-5">
              <div> 1 / 1</div>
            </div>
            <div className="change_page col-md-6 col-sm-6">
              <ul >
                <li className="page_name"><a href="#"></a></li>
                <li className="page_name"><a href="#">1</a></li>
                <li className="page_name"><a href="#"></a></li>
              </ul>          
            </div>
          </div>
        </div>
      </div>
		)
	}
}


