import React, { Component } from 'react';
import './News_list.css';

export default class Create_ques extends Component{
	constructor(props){
    super(props);
    this.state = {
      choose_num: this.props.choose
    }
  }
  onChangeNum(e){
    this.setState({
      choose_num: e
    })
  }
  onChangeNumToo(){
    this.setState({
      choose_num: this.props.choose
    })
  }
  onChangeBody(){
    this.props.onChange(this.state.choose_num)
  } 
	render(){
		return(
			<div id="news_list">
        <div className="col-md-12 col-sm-12">
          <h2 ></h2>
          <h4 ></h4>
          <button className="create_news" onMouseOver={() => {this.onChangeNum(4)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}></button> 
          <div className="ques_box col-md-12 col-sm-12">
            <div>
              <h6 style={{"font-weight": 'bold'}}></h6>
            </div>
            <form>
              <div className="box_body">
                <div className="form-group">
                  <label for="name"></label>
                  <input className="form-control" id="name" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="desc"></label>
                  <input className="form-control" id="desc" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="beginTime"></label>
                  <input className="form-control" id="beginTime" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="endTime"></label>
                  <input className="form-control" id="endTime" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="pageUrl">pageUrl</label>
                  <input className="form-control" id="pageUrl" placeholder="Url" type="text" />
                </div>
              </div>
              <div className="box_footer">
                <input id="submit" value="" type="button" /> 
              </div>
            </form>
          </div>    
        </div>
      </div>
		)
	}
}


