import React, { Component } from 'react';
import './Admin_menu.css'

export default class Admin_menu extends Component {
  constructor(props){
    super(props);
    this.state = {
      choose_num: this.props.choose
    }
  }
  onChangeNum(e){
    
    this.setState({
      choose_num: e 
    });
  }
  onChangeNumToo(){
    this.setState({
      choose_num: this.props.choose
    })
  }
  onChangeBody(){
    this.props.onChange(this.state.choose_num)
  }
  onOut(){
    return alert("")
  }

  render(){

    return (
      <div className="menu">
        
        <div className="admin_img">
          <img src={require("../img/admin.jpg")} />
          <a className="fast_exit" onClick={() => {return alert("")}} href='./'>admin</a>
        </div>
        <div className="nav"> 
          <ul>
            <li>
              <a href="#" className="main-menu" onMouseOver={() => {this.onChangeNum(0)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-dashboard "></i></a>
            </li>
            <li>
              <a href="#"><i className="fa fa-desktop"></i></a>
              <ul>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(1)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-bell"></i></a>
                </li>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(2)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-bell "></i></a>
                </li>                                                     
              </ul>
            </li>
            <li>
              <a href="#"><i className="fa fa-bar-chart-o"></i></a>
              <ul>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(3)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-file"></i></a>
                </li>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(4)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-file"></i></a>
                </li>                                                     
              </ul>
            </li>
            <li>
              <a href="#"><i className="fa fa-file-sound-o"></i></a>
              <ul>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(5)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-file-text"></i></a>
                </li>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(6)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-file-text"></i></a>
                </li>                          
              </ul>
            </li>
            <li>
              <a href="#"><i className="fa fa-group"></i></a>
              <ul>                           
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(7)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-user"></i></a>
                </li>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(8)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-user"></i></a>
                </li>                          
              </ul>
            </li>                    
            <li>
              <a href="#"><i className="fa fa-unlock-alt"></i></a>
              <ul>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(9)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-key"></i></a>
                </li>
                 <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(10)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-key"></i></a>
                </li>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(11)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-key"></i></a>
                </li>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(12)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-key"></i></a>
                </li>
              </ul>
            </li>
            <li>
              <a href="#"><i className="fa fa-gears"></i></a>
              <ul>
                <li>
                  <a href="#" onMouseOver={() => {this.onChangeNum(13)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}><i className="fa fa-gear"></i></a>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </div>

    )
  }
}




