import React, { Component } from 'react';
import './News_list.css';

export default class Create_port extends Component{
	constructor(props){
    super(props);
    this.state = {
      choose_num: this.props.choose
    }
  }
  onChangeNum(e){
    this.setState({
      choose_num: e
    })
  }
  onChangeNumToo(){
    this.setState({
      choose_num: this.props.choose
    })
  }
  onChangeBody(){
    this.props.onChange(this.state.choose_num)
  } 
	render(){
		return(
			<div id="news_list">
        <div className="col-md-12 col-sm-12">
          <h2 ></h2>
          <h4 >REST</h4>
          <button className="create_news" onMouseOver={() => {this.onChangeNum(9)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}></button> 
          <div className="ques_box col-md-12 col-sm-12">
            <div>
              <h6 style={{"font-weight": 'bold'}}></h6>
            </div>
            <form>
              <div className="box_body">
                <div className="form-group">
                  <label for="url"></label>
                  <input className="form-control" id="url" placeholder="RESTurl" type="text" />
                </div>
                <div className="form-group">
                  <label for="method"></label>
                  <div id="method">
                    <input value="GET" name="method" type="checkbox" /> GET &nbsp;&nbsp;&nbsp;&nbsp;
                    <input value="POST" name="method" type="checkbox" /> POST &nbsp;&nbsp;&nbsp;&nbsp;
                    <input value="PUT" name="method" type="checkbox" /> PUT &nbsp;&nbsp;&nbsp;&nbsp;
                    <input value="DELETE" name="method" type="checkbox" /> DELETE
                  </div>
                </div>
                <div className="form-group">
                  <label for="portDesc"></label>
                  <input className="form-control" id="portDesc" placeholder="" type="text" />
                </div>
                
              </div>
              <div className="box_footer">
                <input id="submit" value="" type="button"/> 
              </div>
            </form>
          </div>    
        </div>
      </div>
		)
	}
}


