import React, { Component } from 'react';
import './Reg.css';

class Reg extends Component {
    constructor(props){
        super(props);
        this.state = { // state
            username: '',
            password: ''
        }
    }
    stateChange(e){
        const target = e.target;
        this.setState({
            [target.name]: target.value
        })
    }
    saveUser() {
        const {
            username,
            password
            } = this.state;
        if(!username) return alert('');
        if(!password) return alert('');
    }
    render() {
        return (
            <div onChange={(e)=>this.stateChange(e)}>
                <input name="username" value={this.state.username} placeholder=""/>
                <input name="password" value={this.state.password} placeholder=""/>
                <button onClick={()=>this.saveUser()}></button>
            </div>
        );
    }
}

export default Reg;


