import React, { Component } from 'react';
import './News_list.css';

export default class Create_news extends Component{
	constructor(props){
    super(props);
    this.state = {
      choose_num: this.props.choose
    }
  }
  onChangeNum(e){
    this.setState({
      choose_num: e
    })
  }
  onChangeNumToo(){
    this.setState({
      choose_num: this.props.choose
    })
  }
  onChangeBody(){
    this.props.onChange(this.state.choose_num)
  } 
	render(){
		return(
			<div id="news_list">
        <div className="col-md-12 col-sm-12">
          <h2 ></h2>
          <h4 ></h4>
          <button className="create_news" onMouseOver={() => {this.onChangeNum(1)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}></button> 
          <div className="ques_box col-md-12 col-sm-12">
            <div>
              <h6></h6>
            </div>
            <form>
              <div className="box_body">
                <div className="form-group">
                  <label for="title"></label>
                  <input className="form-control" id="title" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="content"></label>
                  <input className="form-control" id="content" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="url"></label>
                  <input className="form-control" id="url" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="level"></label>
                  <select id="level" className="form-control">
                    <option value="1"></option>
                    <option value="2"></option>
                    <option value="3"></option>
                  </select>
                </div>
                <div className="form-group">
                  <label for="endTime"></label>
                  <input className="form-control" id="beginTime" placeholder="" type="datetime-local" />
                </div>
                <div className="form-group">
                  <label for="endTime"></label>
                  <input className="form-control" id="endTime" placeholder="" type="datetime-local" />
                </div>
              </div>
              <div className="box_footer">
                <input id="submit" value="" type="button" /> 
              </div>
            </form>
          </div>    
        </div>
      </div>
		)
	}
}


