import React, { Component } from 'react';
import './News_list.css';

export default class Syst_para extends Component{
	
	render(){
		return(
			<div id="news_list">
        <div className="col-md-12 col-sm-12">
          <h2 ></h2>
          <h4 ></h4>
          
          <table className="col-md-12 col-sm-12">
            <thead>
              <tr>
                <th rowSpan="1" colSpan="1" style={{'width':100}}>#</th>
                <th rowSpan="1" colSpan="1" style={{'width':350}}></th>
                <th rowSpan="1" colSpan="1" style={{'width':200}}></th>
              </tr>
            </thead>
            <tbody id="tbody">
              <tr>
                <td>1</td>
                <td>ACL</td>
                <td>                                    
                  <button className="action">ACL</button>
                </td>
              </tr>
              <tr>
                <td>2</td>
                <td></td>
                <td>                               
                  <button className="action"></button>
                </td>
              </tr>
              <tr>
                <td>3</td>
                <td></td>
                <td>                               
                  <button className="action"></button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
		)
	}
}


