import React from 'react';

class UserAdd extends React.Component {
    constructor () {
        super();
        this.state = {
            name: '',
            age: 0,
            gender: ''
        };
    }
    handleValueChange (field, value, type = 'string') {
        // typenumbervaluenumber
        if (type === 'number') {
            value = +value;
        }

        this.setState({
            [field]: value
        });
    }
    handleSubmit (e) {
        e.preventDefault();//

        const {name, age, gender} = this.state;
        fetch('http://localhost:3001/user', {
            method: 'post',
            // fetchjsonJSON.stringify
            body: JSON.stringify({//
                name,
                age,
                gender
            }),
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then((res) => res.json())
            .then((res) => {
                // jsonid
                // res.id
                if (res.id) {
                    alert('');
                    this.setState({
                        name: '',
                        age: 0,
                        gender: ''
                    });
                } else {
                    alert('');
                }
            })
            .catch((err) => console.error(err));
    }
    render () {
        const {name, age, gender} = this.state;
        return (
            <div>
                <header>
                    <h2></h2>
                </header>

                <main>
                    <form onSubmit={(e) => this.handleSubmit(e)}>
                        <label></label>
                        <input type="text" value={name} onChange={(e) => this.handleValueChange('name', e.target.value)}/>
                        <br/>
                        <label></label>
                        <input type="number" value={age || ''} onChange={(e) => this.handleValueChange('age', e.target.value, 'number')}/>
                        <br/>
                        <label></label>
                        <select value={gender} onChange={(e) => this.handleValueChange('gender', e.target.value)}>
                            <option value=""></option>
                            <option value="male"></option>
                            <option value="female"></option>
                        </select>
                        <br/>
                        <br/>
                        <input type="submit" value=""/>
                    </form>
                </main>
            </div>
        );
    }
}
export default UserAdd;


