import React from 'react';
import { Link } from 'react-router';

class Home extends React.Component {
    render () {
        return (
            <div>
                <header>
                    <h2>Welcome</h2>
                </header>

                <main>
                    <Link to="/user/add"></Link>
                </main>
            </div>
        );
    }
}

export default Home;


