import React, { Component } from 'react';
import './News_list.css';

export default class Create_users extends Component{
	constructor(props){
    super(props);
    this.state = {
      choose_num: this.props.choose
    }
  }
  onChangeNum(e){
    this.setState({
      choose_num: e
    })
  }
  onChangeNumToo(){
    this.setState({
      choose_num: this.props.choose
    })
  }
  onChangeBody(){
    this.props.onChange(this.state.choose_num)
  } 
	render(){
		return(
			<div id="news_list">
        <div className="col-md-12 col-sm-12">
          <h2 ></h2>
          <h4 ></h4>
          <button className="create_news" onMouseOver={() => {this.onChangeNum(8)}} onMouseOut={() => {this.onChangeNumToo()}} onClick={() => {this.onChangeBody()}}></button> 
          <div className="ques_box col-md-12 col-sm-12">
            <div>
              <h6 style={{"font-weight": 'bold'}}></h6>
            </div>
            <form>
              <div className="box_body">
                <div className="form-group">
                  <label for="accountId"></label>
                  <input className="form-control" id="accountId" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="password"></label>
                  <input className="form-control" id="password" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="description"></label>
                  <input className="form-control" id="description" placeholder="" type="text" />
                </div>
                <div className="form-group">
                  <label for="email"></label>
                  <input className="form-control" id="email" placeholder="" type="text" />
                </div>
                <div class="form-group">
                  <label for="status"></label>
                  <select className="form-control" id="status">
                      <option value="1"></option>
                      <option value="2"></option>
                  </select>
                </div>
                <div className="form-group">
                  <label for="factoryId"></label>
                  <select class="form-control" id="factoryId">
                      <option value="-1"></option>
                      <option value="1"></option>
                  </select>
                </div>
                <div class="form-group">
                  <label for="roleList"></label>
                  <div id="roleList">
                    <table className="table">
                    <tbody>
                      <tr>
                        <td><input value="19" name="role" type="checkbox" /></td>
                        <td><input value="20" name="role" type="checkbox" /></td>
                        <td><input value="22" name="role" type="checkbox" /></td>
                      </tr>
                      <tr>
                        <td><input value="23" name="role" type="checkbox" /></td>
                        <td><input value="24" name="role" type="checkbox" /></td>
                        <td><input value="25" name="role" type="checkbox" /></td>
                      </tr>
                    </tbody>
                    </table>
                  </div>
                </div>
              </div>
              <div className="box_footer">
                <input id="submit" value="" type="button" /> 
              </div>
            </form>
          </div>    
        </div>
      </div>
		)
	}
}


